/// <reference path="../typings/browser.d.ts" />
import {createStore} from "redux"
let store = createStore(
    (state, action) => {
        switch (action.type) {
            case 'INCR':
                return { counter: state.counter + action.by };
            default:
                return state;
        }
    },
    {
        sidebar: [
            {
                id: 1, title: '',
                items: [
                    {
                        id: 11, title: '',href:''
                    },
                    {
                        id: 12, title: '', href: '#/index/general'
                    },
                    {
                        id: 13, title: '', href: '#/index/table'
                    }
                ]
            },
            {
                id: 2, title: '2',
                items: [
                    {
                        id: 21, title: '', href: ''
                    },
                    {
                        id: 22, title: '', href: ''
                    },
                    {
                        id: 23, title: '', href: ''
                    }
                ]
            },
            {
                id: 3, title: '3',
                items: [
                    {
                        id: 31, title: '', href: ''
                    },
                    {
                        id: 32, title: '', href: ''
                    },
                    {
                        id: 33, title: '', href: ''
                    }
                ]
            }
        ]
    });
export {store}


